*, :after, :before {
  box-sizing: inherit;
  backface-visibility: hidden;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

html, body, a {
  cursor: none;
}

@media screen and (width <= 768px) {
  html, body {
    cursor: auto;
  }

  a:hover {
    cursor: pointer;
  }
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-size: 62.5%;
}

body {
  color: #fff;
  font-family: Open Sans, sans-serif;
  font-size: 2.4rem;
}

body::-webkit-scrollbar {
  background-color: #00070d;
  width: .7rem;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px #0000004d;
}

body::-webkit-scrollbar-thumb {
  background-color: #0095c7;
  border-radius: 10rem;
}

h1 {
  font-size: 4.8rem;
}

h2 {
  font-size: 2rem;
}

img {
  max-width: 100%;
}

.screen-cursor {
  z-index: 9950;
  mix-blend-mode: difference;
  position: fixed;
  top: 0;
  left: 0;
}

.screen-cursor-dot {
  background-color: #fff;
  width: .5rem;
  height: .5rem;
}

.screen-cursor-outline {
  will-change: transform;
  border: .2rem solid #fff;
  width: 2rem;
  height: 2rem;
  transition: all .2s cubic-bezier(.215, .61, .355, 1);
}

.screen-cursor-outline.screen-cursor-outline--hover {
  will-change: transform;
  width: 5rem;
  height: 5rem;
}

.screen-cursor-dot, .screen-cursor-outline {
  pointer-events: none;
  z-index: 9975;
  border-radius: 50%;
  position: fixed;
}

@media screen and (width <= 768px) {
  .screen-cursor {
    display: none;
  }
}

.rol-word__content {
  height: 8.5rem;
  overflow: hidden;
}

@media screen and (width <= 1050px) {
  .rol-word__content {
    height: 13rem;
  }
}

@media screen and (width <= 568px) {
  .rol-word__content {
    height: 9rem;
  }
}

.rol-word {
  will-change: transform;
  height: 100%;
  animation: 15s infinite spin-rol_words;
  display: block;
  overflow: hidden;
}

@media screen and (width <= 1050px) {
  .rol-word {
    padding: 1rem 0;
  }
}

@keyframes spin-rol_words {
  28% {
    transform: translateY(0%);
  }

  30% {
    transform: translateY(-112%);
  }

  33.3333% {
    transform: translateY(-100%);
  }

  61% {
    transform: translateY(-100%);
  }

  63% {
    transform: translateY(-212%);
  }

  66.6667% {
    transform: translateY(-200%);
  }

  95% {
    transform: translateY(-200%);
  }

  97% {
    transform: translateY(-312%);
  }

  100% {
    transform: translateY(-300%);
  }
}

.preloader {
  z-index: 9900;
  background-color: #00060e;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  overflow: hidden;
}

.preloader-inner {
  justify-content: space-between;
  width: 25rem;
  display: flex;
}

.preloader__box {
  transform-origin: -50%;
  border-radius: 15%;
  width: 3rem;
  height: 3rem;
  display: block;
  position: relative;
}

.preloader__box:after {
  content: "";
  background-color: #add8e6;
  border-radius: 15%;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: 0 0 10px #1c9fff66;
}

.preloader__box:first-child {
  animation: 1.5s ease-in-out infinite alternate slide;
}

.preloader__box:first-child:after {
  animation: 1.5s ease-in-out infinite alternate color-change;
}

.preloader__box:nth-child(2) {
  animation: 1.5s ease-in-out infinite alternate flip-1;
}

.preloader__box:nth-child(2):after {
  animation: 1.5s ease-in-out infinite alternate squidge-1;
}

.preloader__box:nth-child(3) {
  animation: 1.5s ease-in-out infinite alternate flip-2;
}

.preloader__box:nth-child(3):after {
  animation: 1.5s ease-in-out infinite alternate squidge-2;
}

.preloader__box:nth-child(4) {
  animation: 1.5s ease-in-out infinite alternate flip-3;
}

.preloader__box:nth-child(4):after {
  animation: 1.5s ease-in-out infinite alternate squidge-3;
}

.preloader__box:nth-child(5) {
  animation: 1.5s ease-in-out infinite alternate flip-4;
}

.preloader__box:nth-child(5):after {
  animation: 1.5s ease-in-out infinite alternate squidge-4;
}

@keyframes slide {
  0% {
    background-color: #1795ff;
    transform: translateX(0);
  }

  100% {
    background-color: #23d3fb;
    transform: translateX(21.25rem);
  }
}

@keyframes color-change {
  0% {
    background-color: #1795ff;
  }

  100% {
    background-color: #23d3fb;
  }
}

@keyframes flip-1 {
  0%, 15% {
    transform: rotate(0);
  }

  35%, 100% {
    transform: rotate(-180deg);
  }
}

@keyframes flip-2 {
  0%, 30% {
    transform: rotate(0);
  }

  50%, 100% {
    transform: rotate(-180deg);
  }
}

@keyframes flip-3 {
  0%, 45% {
    transform: rotate(0);
  }

  65%, 100% {
    transform: rotate(-180deg);
  }
}

@keyframes flip-4 {
  0%, 60% {
    transform: rotate(0);
  }

  80%, 100% {
    transform: rotate(-180deg);
  }
}

@keyframes squidge-1 {
  5% {
    transform-origin: bottom;
    transform: scaleX(1)scaleY(1);
  }

  15% {
    transform-origin: bottom;
    transform: scaleX(1.3)scaleY(.7);
  }

  25%, 20% {
    transform-origin: bottom;
    transform: scaleX(.8)scaleY(1.4);
  }

  55%, 100% {
    transform-origin: top;
    transform: scaleX(1)scaleY(1);
  }

  40% {
    transform-origin: top;
    transform: scaleX(1.3)scaleY(.7);
  }
}

@keyframes squidge-2 {
  20% {
    transform-origin: bottom;
    transform: scaleX(1)scaleY(1);
  }

  30% {
    transform-origin: bottom;
    transform: scaleX(1.3)scaleY(.7);
  }

  40%, 35% {
    transform-origin: bottom;
    transform: scaleX(.8)scaleY(1.4);
  }

  70%, 100% {
    transform-origin: top;
    transform: scaleX(1)scaleY(1);
  }

  55% {
    transform-origin: top;
    transform: scaleX(1.3)scaleY(.7);
  }
}

@keyframes squidge-3 {
  35% {
    transform-origin: bottom;
    transform: scaleX(1)scaleY(1);
  }

  45% {
    transform-origin: bottom;
    transform: scaleX(1.3)scaleY(.7);
  }

  55%, 50% {
    transform-origin: bottom;
    transform: scaleX(.8)scaleY(1.4);
  }

  85%, 100% {
    transform-origin: top;
    transform: scaleX(1)scaleY(1);
  }

  70% {
    transform-origin: top;
    transform: scaleX(1.3)scaleY(.7);
  }
}

@keyframes squidge-4 {
  50% {
    transform-origin: bottom;
    transform: scaleX(1)scaleY(1);
  }

  60% {
    transform-origin: bottom;
    transform: scaleX(1.3)scaleY(.7);
  }

  70%, 65% {
    transform-origin: bottom;
    transform: scaleX(.8)scaleY(1.4);
  }

  100%, 100% {
    transform-origin: top;
    transform: scaleX(1)scaleY(1);
  }

  85% {
    transform-origin: top;
    transform: scaleX(1.3)scaleY(.7);
  }
}

