}

.nav-mobile__list--link:hover, .nav-mobile__list--link:focus, .nav-mobile__list--link:active {
  color: #00070d;
  background-position: 100%;
  outline: none;
  transform: translateX(.5rem);
}

.navigation__button {
  z-index: 1500;
  color: #fff;
  display: none;
  position: relative;
}

.navigation__button:hover {
  cursor: pointer;
}

.navigation__icon, .navigation__icon:after, .navigation__icon:before {
  background-color: #fff;
  border-radius: 10rem;
  width: 3rem;
  height: .3rem;
  transition: all .3s;
  display: inline-block;
}

.navigation__icon {
  position: relative;
}

.navigation__icon:before {
  content: "";
  position: absolute;
  top: -1rem;
}

.navigation__icon:after {
  content: "";
  position: absolute;
  top: 1rem;
}

.navigation__button:hover .navigation__icon:before {
  top: -1.3rem;
}

.navigation__button:hover .navigation__icon:after {
  top: 1.3rem;
}

.navigation__mobile {
  opacity: 0;
  z-index: 1400;
  will-change: transform;
  width: 0;
  height: 100vh;
  transition: all .8s cubic-bezier(.68, -.55, .265, 1.55);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
}

.navigation__background {
  opacity: 1;
  will-change: transform;
  width: 3rem;
  height: 3rem;
  transition: width .8s cubic-bezier(.68, -.55, .265, 1.55), height .8s cubic-bezier(.68, -.55, .265, 1.55);
  position: fixed;
  top: 0;
  right: -5rem;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
}

.navigation__background-video {
  z-index: 1100;
  object-fit: cover;
  background-image: url("bg-image-2.c183826a.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

.navigation__checkbox, .navigation__checkbox:checked ~ .nav-bar__list {
  display: none;
}

.navigation__checkbox:checked ~ .navigation__mobile, .navigation__checkbox:checked ~ .navigation__mobile .nav-mobile__list {
  opacity: 1;
  width: 100%;
}

.navigation__checkbox:checked ~ .navigation__background {
  width: calc(100vw + 5rem);
  height: 100vh;
}

.navigation__checkbox:checked ~ .navigation__button .navigation__icon {
  background-color: #0000;
}

.navigation__checkbox:checked ~ .navigation__button .navigation__icon:before {
  top: 0;
  transform: rotate(135deg);
}

.navigation__checkbox:checked ~ .navigation__button .navigation__icon:after {
  top: 0;
  transform: rotate(-135deg);
}

.navigation__checkbox:checked ~ .navigation__button {
  display: block;
  transform: translateY(-.75rem);
}

@media screen and (width <= 768px) {
  .nav-bar__list {
    display: none;
